<template>
    <div class="empleadosDepartamentos mt-2">
        <p class="empleadosDepartamentos__breadcrum"><router-link to="/">Inicio</router-link> / <router-link
                to="/departamentos">Departamento</router-link> / Empleados Departamento</p>
        <b-container>
            <table class="table table-hover mt-5 empleadosDepartamentos__table">
                <thead>
                    <tr>
                        <th>Identificador</th>
                        <th>Cédula (Número nacional)</th>
                        <th>Título de trabajo</th>
                        <th>Fecha de contratación</th>
                        <th>horas vacaciones</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="emple in emplePaginados" :key="emple.businessentityid">
                        <td>{{ emple.businessentityid }}</td>
                        <td>{{ emple.nationalidnumber }}</td>
                        <td>{{ emple.jobtitle }}</td>
                        <td>{{ emple.hiredate }}</td>
                        <td>{{ emple.vacationhours }}</td>
                    </tr>
                </tbody>
            </table>
        </b-container>
        <b-container class="d-flex justify-content-center empleadosDepartamentos__pagination">
            <b-pagination v-model="pagina_actual" :total-rows="lista_empleados_filtrados.length" :per-page="cantidad_por_pagina"></b-pagination>
        </b-container>
    </div>
</template>
    
<script>
import { mapState } from 'vuex';
export default {
    name: 'EmpleadosDepartamentos',
    props: {
        id: String
    },
    data() {
        return {
            lista_empleados_filtrados: [],
            pagina_actual: 1,
            cantidad_por_pagina: 5,
        };
    },
    components: {
    },
    computed: {
        ...mapState('human_resources_module', ['empleados']),
        ...mapState('human_resources_module', ['empleados_departamentos']),
        emplePaginados() {
            let inicio = (this.pagina_actual - 1) * this.cantidad_por_pagina;
            let fin = inicio + this.cantidad_por_pagina;
            return this.lista_empleados_filtrados.slice(inicio, fin);
        },
    },
    methods: {
        filtrarPorIdDepto: function () {
            for (let index = 0; index < this.empleados_departamentos.length; index++) {
                console.log('1')
                let element = this.empleados_departamentos[index];
                if (element.DepartmentID == this.id) {
                    for (let j = 0; j < this.empleados.length; j++) {
                        console.log('2')
                        let empleado = this.empleados[j];
                        if (element.BusinessEntityID == empleado.businessentityid) {
                            this.lista_empleados_filtrados.push(empleado)
                        }
                    }

                }
            }
        },

    },
    created() {
        this.filtrarPorIdDepto();
    }
}
</script>
    
<style scoped>
table {
    text-align: left !important;
}

.empleadosDepartamentos__breadcrum {
    text-align: left !important;
    margin-left: 20px !important;
}
</style>
    