<template>
    <div class="empleadosDepartamentos mt-2">
        <p class="empleadosDepartamentos__breadcrum"><router-link to="/">Inicio</router-link> / <router-link to="/empleados">Empleados</router-link> /
            Detalle empleado</p>
        <b-container class="empleadosDepartamentos__container">
            <ul class="nav nav-tabs empleadosDepartamentos__nav" id="myTab" role="tablist">
                <li class="nav-item" role="presentation">
                    <button class="nav-link active" id="home-tab" data-bs-toggle="tab" data-bs-target="#home-tab-pane"
                        type="button" role="tab" aria-controls="home-tab-pane" aria-selected="true">Detalle</button>
                </li>
                <li class="nav-item" role="presentation">
                    <button class="nav-link" id="profile-tab" data-bs-toggle="tab" data-bs-target="#profile-tab-pane"
                        type="button" role="tab" aria-controls="profile-tab-pane" aria-selected="false">Información de
                        pago</button>
                </li>

            </ul>
            <div class="tab-content empleadosDepartamentos__tab" id="myTabContent">
                <div class="tab-pane fade show active" id="home-tab-pane" role="tabpanel" aria-labelledby="home-tab"
                    tabindex="0">
                    <div class="row leftAll">
                        <hr>
                        <p>Identificador: {{ identificadorE }}</p>
                        <hr>
                        <p>Número nacional: {{ nNacional }}</p>
                        <hr>
                        <p>Título de trabajo: {{ tituloT }}</p>
                        <hr>
                        <p>Fecha de nacimiento: {{ fechaN | formatDate}}</p>
                        <hr>
                        <p>Estado Civíl: {{ estadoC }}</p>
                        <hr>
                        <p>Género: {{ genero }}</p>
                        <hr>
                        <p>Fecha de contratación:{{ fechacC | formatDate}}</p>
                        <hr>
                        <p>Horas de vacaciones: {{ horasV }}</p>
                        <hr>
                    </div>

                </div>
                <div class="tab-pane fade" id="profile-tab-pane" role="tabpanel" aria-labelledby="profile-tab" tabindex="0">
                    <div class="row leftAll">
                        <hr>
                        <p>Identificador: {{ identificadorE }}</p>
                        <hr>
                        <p>Fecha de cambio de tarifa: {{ fechaCambio | formatDate}}</p>
                        <hr>
                        <p>Tarifa: {{ tarifa }}</p>
                        <hr>
                        <p>Frecuencia de pago: {{ frecuencia }}</p>
                        <hr>
                    </div>
                </div>
            </div>
        </b-container>
    </div>
</template>
    
<script>
import { mapState } from 'vuex';
export default {
    name: 'EmpleadosDepartamentos',
    props: {
        id: String
    },
    data() {
        return {
            identificadorE: '',
            nNacional: '',
            tituloT: '',
            fechaN: '',
            estadoC: '',
            genero: '',
            fechacC: '',
            horasV: '',
            fechaCambio: '',
            tarifa: '',
            frecuencia: ''

        };
    },
    components: {
    },
    computed: {
        ...mapState('human_resources_module', ['empleados']),
        ...mapState('human_resources_module', ['historial_pago_empleados']),
    },
    methods: {
        cracionObjetos: function () {
            let objeto = {}
            for (let index = 0; index < this.historial_pago_empleados.length; index++) {
                let element = this.historial_pago_empleados[index];
                if (element.BusinessEntityID == this.id) {
                    for (let j = 0; j < this.empleados.length; j++) {
                        let empleado = this.empleados[j];
                        if (element.BusinessEntityID == empleado.businessentityid) {
                            this.identificadorE = empleado.businessentityid;
                            this.nNacional = empleado.nationalidnumber;
                            this.tituloT = empleado.jobtitle;
                            this.fechaN = empleado.birthdate;
                            this.estadoC = empleado.maritalstatus;
                            this.genero = empleado.gender;
                            this.fechacC = empleado.hiredate;
                            this.horasV = empleado.vacationhours;
                            this.fechaCambio = element.RateChangeDate;
                            this.tarifa = element.Rate;
                            this.frecuencia = element.PayFrequency;
                        }
                    }

                }
            }
        }

    },
    created() {
        this.cracionObjetos();
    },
    filters: {
        formatDate: function (value) {
            if (!value) return '';
            const date = new Date(value);
            const year = date.getFullYear();
            const month = String(date.getMonth() + 1).padStart(2, '0');
            const day = String(date.getDate()).padStart(2, '0');
            return `${day}-${month}-${year}`;
        },
    },
}
</script>
    
<style scoped>
.empleadosDepartamentos__breadcrum {
    text-align: left !important;
    margin-left: 20px !important;
}

.leftAll {
    text-align: left !important;
}
</style>
    