<template>
  <div class="empleados mt-2">
    <p class="empleados__breadcrum"><router-link to="/">Inicio</router-link> / Empleados</p>
    <b-container class="empleados__container">
      <b-row>
        <b-col>
          <b-form-group id="identificador" label="Identificador (BusinessEntityID)" label-for="inputId">
            <b-form-input id="inputId" v-model="id_buscar" type="text"></b-form-input>
          </b-form-group>
        </b-col>
        <b-col>
          <label for="fechaInicio">Fecha contratación (inicio rango)</label>
          <b-form-datepicker id="fechaInicio" v-model="fecha_i" class="mb-2" placeholder="dd-mm-aaaa"></b-form-datepicker>
        </b-col>
        <b-col>
          <label for="fechaFin" > Fecha contratación (fin rango)</label>
          <b-form-datepicker id="fechaFin" v-model="fecha_f" class="mb-2" placeholder="dd-mm-aaaa"></b-form-datepicker>
        </b-col>
      </b-row>
      <br>
      <b-row>
        <b-button variant="primary" id="btnFiltrar">Filtrar</b-button>
      </b-row>
      <table class="table table-hover mt-5 empleados__table">
        <thead>
          <tr>
            <th>Identificador</th>
            <th>Cédula (Número nacional)</th>
            <th>Fecha de contratación</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="empl in emplPaginados" :key="empl.businessentityid">
            <td>{{ empl.businessentityid }}</td>
            <td>{{ empl.nationalidnumber }}</td>
            <td>{{ empl.hiredate }}</td>
            <td><b-button variant="primary" v-on:click="redirigir(empl.businessentityid)">Ver detalle</b-button></td>
          </tr>
        </tbody>
      </table>
    </b-container>
    <b-container class="d-flex justify-content-center empleados__pagination">
      <b-pagination v-model="pagina_actual" :total-rows="empleados.length" :per-page="cantidad_por_pagina"></b-pagination>
    </b-container>
  </div>
</template>
    
<script>
import { mapActions, mapState } from 'vuex';
export default {
  name: 'Empleados',
  props: {},
  data() {
    return {
      pagina_actual: 1,
      cantidad_por_pagina: 5,
      id_buscar: '',
      fecha_i: '',
      fecha_f: '',
    };
  },
  components: {
  },
  computed: {
    ...mapState('human_resources_module', ['empleados']),
    emplPaginados() {
      let inicio = (this.pagina_actual - 1) * this.cantidad_por_pagina;
      let fin = inicio + this.cantidad_por_pagina;
      return this.empleados.slice(inicio, fin);
    },
  },
  methods: {
    redirigir: function (id) {
      this.$router.push(`/detalleempleado/`+id)
    },
  },
  created() {
   
  },
  mounted() { }
}
</script>
    
<style scoped>
table {
  text-align: left !important;
}

.empleados__breadcrum {
  text-align: left !important;
  margin-left: 20px !important;
}

#btnFiltrar{
  width: 80px;
}
</style>
    