<template>
  <div class="turnos mt-2">
      <p  class="turnos__breadcrum"><router-link to="/">Inicio</router-link> / Turnos</p>
      <b-container class="turnos__container">
          <table class="table table-hover mt-5 turnos__table">
              <thead>
                  <tr>
                      <th>Identificador</th>
                      <th>Nombre</th>
                      <th>Hora entrada</th>
                      <th>Hora salida</th>
                  </tr>
              </thead>
              <tbody>
                  <tr v-for="turno in turnosPaginados" :key="turno.ShiftID">
                      <td>{{ turno.ShiftID }}</td>
                      <td>{{ turno.Name }}</td>
                      <td>{{ turno.StartTime | formatTime }}</td>
                      <td>{{ turno.EndTime | formatTime }}</td>
                  </tr>
              </tbody>
          </table>
      </b-container>
      <b-container class="d-flex justify-content-center turnos__pagination">
          <b-pagination v-model="pagina_actual" :total-rows="turnos.length" :per-page="cantidad_por_pagina"></b-pagination>
      </b-container>
  </div>
</template>
  
<script>
import { mapActions, mapState } from 'vuex';
export default {
  name: 'Departamentos',
  props: {},
  data() {
      return {
          pagina_actual: 1,
          cantidad_por_pagina: 5,
      };
  },
  components: {
  },
  computed: {
      ...mapState('human_resources_module', ['turnos']),
      turnosPaginados() {
          let inicio = (this.pagina_actual - 1) * this.cantidad_por_pagina;
          let fin = inicio + this.cantidad_por_pagina;
          return this.turnos.slice(inicio, fin);
      },
  },
  methods: {
      ...mapActions('human_resources_module', ['getTurnos']),
  },
  created() {
      this.getTurnos();
  },
  mounted() { },
  filters: {
    formatTime(value) {
      if (!value) return '';
      return value.substring(0, 5); 
    },
  },
}
</script>
  
<style scoped>
table {
  text-align: left !important;
}

.turnos__breadcrum {
  text-align: left !important;
  margin-left: 20px !important;
}
</style>
  