import axios from "axios";
const human_resources_module = {
    namespaced: true,
    state: {
        departamentos:[],
        empleados:[],
        turnos:[],
        empleados_departamentos:[],
        historial_pago_empleados:[],
    },
    getters: {
        
    },
    mutations: {
        GET_DEPARTAMENTOS: (state, respuesta) => {
            state.departamentos = respuesta;
        },
        GET_EMPLEADOS: (state, respuesta) => {
            state.empleados = respuesta;
        },
        GET_TURNOS: (state, respuesta) => {
            state.turnos = respuesta;
        },
        GET_EMPLEADOS_DEPARTAMENTO: (state, respuesta) => {
            state.empleados_departamentos = respuesta;
        },
        GET_HISTORIAL_PAGO_EMPLEADOS: (state, respuesta) => {
            state.historial_pago_empleados = respuesta;
        },
    },
    actions: {
        getDepartamentos: (context) => {
            let url = 'department.json';
            axios.get(url)
                .then(respuesta => {
                    context.commit('GET_DEPARTAMENTOS', respuesta.data);
                })
                .catch(error => {
                    console.log(error);
                })
        },
        getEmpleados: (context) => {
            let url = 'employee.json';
            axios.get(url)
                .then(respuesta => {
                    context.commit('GET_EMPLEADOS', respuesta.data);
                })
                .catch(error => {
                    console.log(error);
                })
        },
        getTurnos: (context) => {
            let url = 'shift.json';
            axios.get(url)
                .then(respuesta => {
                    context.commit('GET_TURNOS', respuesta.data);
                })
                .catch(error => {
                    console.log(error);
                })
        },
        getEmpleadosDepartamento: (context) => {
            let url = '/employeedepartmenthistory.json';
            axios.get(url)
                .then(respuesta => {
                    context.commit('GET_EMPLEADOS_DEPARTAMENTO', respuesta.data);
                })
                .catch(error => {
                    console.log(error);
                })
        },
        getHistorialPagoEmpleados: (context) => {
            let url = '/employeepayhistory.json';
            axios.get(url)
                .then(respuesta => {
                    context.commit('GET_HISTORIAL_PAGO_EMPLEADOS', respuesta.data);
                })
                .catch(error => {
                    console.log(error);
                })
        }
    },
    modules: {}
};

export default human_resources_module;