import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import Departamentos from '../components/Departamentos.vue'
import EmpleadosDepartamento from '../components/EmpleadosDepartamento.vue'
import Empleados from '../components/Empleados.vue'
import DetalleEmpleado from '../components/DetalleEmpleado.vue'
import Turnos from '../components/Turnos.vue'
import NotFound from '../components/NotFound.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/departamentos',
    name: 'departamentos',
    component: Departamentos
  },
  {
    path: '/empleados',
    name: 'empleados',
    component: Empleados
  },
  {
    path: '/turnos',
    name: 'turnos',
    component: Turnos
  },
  {
    path:'/empleadosdepartamento/:id',
    name:'empleadosdepartamento',
    props:true,
    component:EmpleadosDepartamento
  },
  {
    path:'/detalleempleado/:id',
    name:'detalleempleado',
    props:true,
    component:DetalleEmpleado
  },
  {
    path: '/:catchAll(.*)',
    name: 'notfound',
    component: NotFound
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
