<template>
    <div class="departamentos mt-2">
        <p  class="departamentos__breadcrum"><router-link to="/">Inicio</router-link> / Departamentos</p>
        <b-container>
            <table class="table table-hover mt-5 departamentos__table">
                <thead>
                    <tr>
                        <th>Identificador</th>
                        <th>Nombre departamento</th>
                        <th>Grupo</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="depto in deptosPaginados" :key="depto.DepartmentID">
                        <td>{{ depto.DepartmentID }}</td>
                        <td>{{ depto.Name }}</td>
                        <td>{{ depto.GroupName }}</td>
                        <td><b-button variant="primary" v-on:click="redirigir(depto.DepartmentID)">Ver empleados</b-button></td>
                    </tr>
                </tbody>
            </table>
        </b-container>
        <b-container class="d-flex justify-content-center departamentos__pagination">
            <b-pagination v-model="pagina_actual" :total-rows="departamentos.length" :per-page="cantidad_por_pagina"></b-pagination>
        </b-container>
    </div>
</template>
    
<script>
import { mapActions, mapState } from 'vuex';
export default {
    name: 'Departamentos',
    props: {},
    data() {
        return {
            pagina_actual: 1,
            cantidad_por_pagina: 5,
        };
    },
    components: {
    },
    computed: {
        ...mapState('human_resources_module', ['departamentos']),
        deptosPaginados() {
            let inicio = (this.pagina_actual - 1) * this.cantidad_por_pagina;
            let fin = inicio + this.cantidad_por_pagina;
            return this.departamentos.slice(inicio, fin);
        },
    },
    methods: {
        ...mapActions('human_resources_module', ['getDepartamentos']),
        redirigir: function (id) {
      this.$router.push(`/empleadosdepartamento/`+id)
    },
    },
    created() {
        this.getDepartamentos();
    },
    mounted() { }
}
</script>
    
<style scoped>
table {
    text-align: left !important;
}

.departamentos__breadcrum {
    text-align: left !important;
    margin-left: 20px !important;
}
</style>
    